.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vw;
  min-height: 90vh;
  margin: 0 auto;
  overflow: hidden;
  background-color:azure;
}

img{
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin: 50px auto 20px;
}
.name{
  color: goldenrod;
  font-weight: bold;
  text-transform: uppercase;
}
.job{
  text-transform: capitalize;
  color: #444;
}
.quote{
  padding: 10px 20px;
}
.icon{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
button{
  justify-content: space-between;
  color: #fff;
  background-color: goldenrod;
  border: none;
  padding: 5px 10px;
  margin: 20px;
}
.prev{justify-self: start; align-self: start;}
.people{
  display: flex;
  text-align: center;
  position: relative;
  width: 100%;
  height: 450px;
}
article{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all .5s ease-in ;
}
article.activeSlide{
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide{
  transform: translateX(-100%);
}
article.nextSlide{
  transform: translateX(100%);
}